.App {
  text-align: center;
  padding: 2rem;
}

.header {
  display: flex;
  justify-content: end;
  flex-direction: column;
}
.df {
  display: flex;
  justify-content: end;
}
.main {
  border-top: 1px solid #000;
}
.form {
  display: flex;
  align-items: end;
}

.btn-s {
  height: 40px;
  margin: 8px;
  cursor: pointer;
}
.list_prod {
  padding: 0;
}
.list_prod li {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 100px;
  gap: 5px;
}
.list_prod li p {
  line-height: 18px;
  text-align: left;
  padding: 0;
}
.name_component {
  font-size: 18px;
  font-weight: 600;
  max-width: min-content;
}
.price {
  border-top: 1px solid #000;
  text-align: right;
}
.bg-g {
  background-color: #a6e673;
  color: #fff;
  border: 1px solid #8bb16c;
  transition: all 0.3s ease-in-out;
}
.bg-g:hover {
  background-color: #7db64e;
}
.bg-or {
  background-color: #dd7a38;
  color: #fff;
  border: 1px solid #ec3b2f;
  transition: all 0.3s ease-in-out;
}
.bg-or:hover {
  background-color: #d16218;
}

@media screen and (max-width: 900px) {
  .form {
    display: flex;
    align-items: end;
    flex-wrap: wrap;
  }
  .list_prod li {
    display: grid;
    grid-template-columns: 1fr 1fr 80px;

    gap: 5px;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #000;
  }
  .terminal {
    width: 100%;
  }
  .terminal span {
    font-weight: 600;
  }
  .btn-s {
    display: block;
    height: 30px;
    margin: 1px;
    cursor: pointer;
  }
}
